.App {
    text-align: center;
}
  
.Letters {
    margin: auto;
    width: 100%;
    height: 500px;
}
  
.Board {
    overflow: hidden;
    height: 100px;
}
  
.Word {
    margin-top: 15px;
    margin-right: 30px;
    display: inline-block;
}
  
.YourWords {
  float: left;
  height: 100%;
  width: 50%;
}

.YourWords   .Letter {
  background: -webkit-gradient(linear, left bottom, left top, from(#eff675), to(#f0e946));
  background: -webkit-linear-gradient(bottom, #eff675, #f0e946);
  background: -o-linear-gradient(bottom, #eff675, #f0e946);
  background: linear-gradient(0deg, #eff675, #f0e946);    
}

.OpponentWords {
  float: right;
  width: 50%;
  height: 100%;
}

.OpponentWords   .Letter {
  background: -webkit-gradient(linear,left bottom, left top,from(#2aa107),to(#258e14));
  background: -webkit-linear-gradient(bottom,#2aa107,#258e14);
  background: -o-linear-gradient(bottom,#2aa107,#258e14);
  background: linear-gradient(0deg,#2aa107,#258e14);
}

.AllWords {
  width: 100%;
  padding-left: 30px;
  padding-right: 30px;
  background: #27870a
}


