.WordBuilder {
    height: 80px;
}

.WordBuilder > .Letter {
    height: 60px;
    width: 60px;
    font-size: 20px;  
}
  
.WordBuilder > .Letter > .LetterText {
    position: relative;
    font-size: 30px;
    top: 17%;
    -webkit-transform: translateY(10%);
        -ms-transform: translateY(10%);
            transform: translateY(10%);
}
  
.WordBuilder.invalid > .Letter {
    background: -webkit-gradient(linear, left bottom, left top, from(#dadada), to(#cdcdcd));
    background: -webkit-linear-gradient(bottom, #dadada, #cdcdcd);
    background: -o-linear-gradient(bottom, #dadada, #cdcdcd);
    background: linear-gradient(0deg, #dadada, #cdcdcd);
}

.WordBuilder.invalid > .Letter > .LetterText {
    color: gray;
}
  


