.Letter .LetterText {
    position: relative;
    font-size: 20px;
    top: 50%;
    -webkit-transform: translate(0%, 22%);
        -ms-transform: translate(0%, 22%);
            transform: translate(0%, 22%);
    font-family: sans-serif;
    font-weight: normal;
  }
  
  .Letter {
      min-width: 30px;
      min-height: 30px;
      margin-right: 3px;
      margin-bottom: 3px;
      display: inline-block;
      text-align: center;
      border-width: 2px;
      border-color: black;
      border-radius: 10%;
      color: black;
      font-weight: bold;
      background: -webkit-gradient(linear, left bottom, left top, from(#f0e8d7), to(#e9ddc5));
      background: -webkit-linear-gradient(bottom, #f0e8d7, #e9ddc5);
      background: -o-linear-gradient(bottom, #f0e8d7, #e9ddc5);
      background: linear-gradient(0deg, #f0e8d7, #e9ddc5);
  }


